.testimonials {
  min-height: 650px;
  background: #f3f3f3;
  padding: 75px 0 0 0;
}

.testimonials__title {
  color: #333b46;
  font-family: "Source Sans Pro";
  font-size: 20px;
  font-weight: 400;
  width: 300px;
  letter-spacing: 4px;
  margin: 0 auto;
  text-align: center;
}

.testimonials__title-text {
  color: #81868e;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  width: 460px;
  line-height: 25px;
  margin: 35px auto 50px auto;
  text-align: center;
}

.testimonials .comments {
  max-width: 1170px;
  margin: 0 auto;
  min-height: 315px;
  background: #fff;
}

.testimonials .comments__slide {
  padding: 50px 95px 0 95px;
  width: 100%;
  outline: none;
  cursor: pointer;
}

.testimonials .comments__slide img {
  display: block;
  float: left;
  width: 135px;
  height: 135px;
}

.testimonials .comments__slide-info {
  vertical-align: top;
  padding-top: 10px;
  width: 77%;
  float: right;
}

.testimonials .comments__slide-info p {
  color: #81868e;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  word-wrap: break-word;
  width: 100%;
}

.testimonials .comments__slide-info h3 {
  color: #19bd9a;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 700;
  margin-top: 15px;
}

.testimonials .comments__slide-info h4 {
  color: #464646;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  margin-top: 5px;
}

.testimonials .slick-dots {
  position: inherit;
}

@media all and (max-width: 950px) {
  .testimonials .comments__slide img {
    margin: 0 auto;
    float: none;
  }
  .testimonials .comments__slide-info {
    float: none;
    text-align: center;
    margin: 0 auto;
  }
}
